import { FC, useEffect } from "react";
import DailyChat from "./daily-chat/daily-chat";
import { Wrapper } from "./styles-correspondences";
import { CorrespondencesProps } from "./types-correspondences";
const Correspondences: FC<CorrespondencesProps> = () => {
  useEffect(() => {
    const handler = (event:any) => {
      console.log("should not fire");
    };

    document.addEventListener("contextmenu", handler);

    return () => {
      document.removeEventListener("contextmenu", handler);
    };
  }, []);
  return (
    <Wrapper  onContextMenu={(event) => {
      event.stopPropagation();
      event.preventDefault();
      console.log("should fire");
      return false;
    }}>
      <DailyChat />
      <DailyChat />
    </Wrapper>
  );
};

export default Correspondences;
