import { Popover } from "@mui/material";
import { FC, useState } from "react";
import { Wrapper } from "./styles-functions-in-popover";
import { FunctionsInPopoverProps } from "./types-functions-in-popover";
const FunctionsInPopover: FC<FunctionsInPopoverProps> = ({
  open,
  handleClose,
  anchorEl,
  children,
  transformOrigin,
  anchorOrigin
}) => {
  const id = open ? "simple-popover" : undefined;
  return (
    <Wrapper>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={anchorOrigin}
        transformOrigin={transformOrigin}
      >
        {children}
      </Popover>
    </Wrapper>
  );
};

export default FunctionsInPopover;
