import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  padding: 12px 0;
  cursor: pointer;
  align-items: center;
`;
export const AvatarPart = styled.div`
  background: #0496ff;
  border-radius: 50%;
  margin-right: 8px;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 48px;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
  }
  @media (max-width: 1370px) {
    width: 35px;
    height: 35px;
    img {
      width: 35px;
    }
  }
`;
export const InfoPart = styled.div`
  width: 100%;
`;
export const TopInfo = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;
export const ChatName = styled.h3`
  font-size: 16px;
  font-weight: 600;
  @media (max-width: 1370px) {
    font-size: 13px;
  }
`;
export const ChatTime = styled.p`
  font-size: 12px;
  font-weight: 400;
`;
export const BottomInfo = styled.div`
  display: flex;
  align-items: center;
`;
export const LastMessage = styled.div`
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  margin-top: 3px;
  span {
    font-weight: 400;
    margin-left: 3px;
  }
`;
export const ChatNameAndType = styled.div`
  img {
    width: 22px;
    height: 22px;
    margin-right: 10px;
  }
  display: flex;
  align-items: center;
  @media (max-width: 1370px) {
    img {
      width: 18px;
      height: 18px;
      margin-right: 6px;
    }
  }
`;
