import styled from "styled-components";
import { MessageStyleProps } from "./types-message";
import meIcon from "@assets/images/dashboard/chat/me.svg";
import youIcon from "@assets/images/dashboard/chat/you.svg";
export const Wrapper = styled.div`
  max-width: 100%;
  padding: 9px;
  display: flex;
`;
export const MessageContent = styled.div<Pick<MessageStyleProps, "byMe">>`
  position: relative;
  padding: 4px 8px;
  ${(props) =>
    props.byMe
      ? "margin-left:auto; color:#fff;"
      : "margin-right:auto; color:#2C2C2E;"}

  h3 {
    margin-bottom: 7px;
    font-size: 14px;
    font-weight: 600;
    color: #2c2c2e;
    span {
      font-size: 12px;
      font-weight: 400;
      margin-left: 10px;
      color: #666668;
    }
  }
  p {
    ${(props) => (props.byMe ? "color:#fff" : "color:#666668")}
  }
  max-width: 50%;
  width: max-content;
  border-radius: ${(props) =>
    props.byMe ? "6px 0 6px 6px" : " 0 6px 6px 6px"};
  background: ${(props) => (props.byMe ? "#007AFF" : "#F2F2F7")};
  ::after {
    content: "";
    width: 8.72px;
    height: 11.12px;
    position: absolute;
    ${(props) => (props.byMe ? "top:0; right:-7px;" : "left:-8px;top:0;")}
    background: url(${(props) => (props.byMe ? meIcon : youIcon)});
  }
`;
export const BottomPart = styled.div`
  display: flex;
  h4 {
    font-size: 14px;
    font-weight: 400;
  }
  h4 {
    margin-right: 10px;
  }
  p {
    font-size: 12px;
    font-weight: 400;
    margin-top: 15px;
    margin-left: auto;
  }
  @media (max-width: 1370px) {
    p {
      font-size: 10px;
    }
    h4 {
      font-size: 13px;
    }
  }
`;
export const AvatarPart = styled.div`
  img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }
`;
export const PopoverFunctions = styled.div`
  .for-border {
    width: 100%;
    border: 2px solid #0000001a;
  }
`;
export const PopoverItem = styled.div`
  display: flex;
  padding: 17px 21px;
  cursor: pointer;
  :hover {
    background-color: #f4f4f4;
  }
  img {
    margin-right: 8px;
  }
  p {
    font-size: 16px;
    font-weight: 400;
  }
`;

export const PopoverFunctionsForSaw = styled.div`
  max-height: 215px;
  ::-webkit-scrollbar {
    width: 0;
  }
  overflow-y: auto;
`;
export const PopoverItemForSaw = styled.div`
  display: flex;
  padding: 8px 21px;
  align-items: center;
  img[alt="avatar"] {
    margin-right: 10px;
    width: 32px;
    object-fit: cover;
    border-radius: 50%;
    height: 32px;
  }
  p {
    font-size: 16px;
    font-weight: 400;
    margin-right: 8px;
  }
`;
