import { FC, useState } from "react";
import FunctionsInPopover from "../functions-in-popover/functions-in-popover";
import { MessageProps } from "./types-message";
import {
  AvatarPart,
  BottomPart,
  MessageContent,
  PopoverFunctions,
  PopoverFunctionsForSaw,
  PopoverItem,
  PopoverItemForSaw,
  Wrapper,
} from "./styels-message";
import returnIcon from "@assets/images/dashboard/chat/return.svg";
import editIcon from "@assets/images/dashboard/chat/edit.svg";
import taskIcon from "@assets/images/dashboard/chat/task.svg";
import addAssemblyIcon from "@assets/images/dashboard/chat/add-assembly.svg";
import notificationIcon from "@assets/images/dashboard/chat/notification.svg";
import removeIcon from "@assets/images/dashboard/chat/remove.svg";
import sawIcon from "@assets/images/dashboard/chat/saw.svg";
import doubleClickIcon from "@assets/images/dashboard/chat/read-receipt.svg";
import OpenTaskModal from "@components/common/open-task-modal/open-task-modal";
import AddNoteModal from "../../../add-note-modal/add-note-modal";
import RemoveModal from "@components/common/remove-modal/remove-modal";
const Message: FC<MessageProps> = ({ messageData }) => {
  const [anchorElForSaw, setAnchorElForSaw] =
    useState<HTMLButtonElement | null>(null);
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  const [openAddTask, setOpenAddTask] = useState(false);
  const [openAddNote, setOpenAddNote] = useState(false);
  const [openRemoveModal, setOpenRemoveModal] = useState(false);

  const doubleClick = (event: any) => {
    event.button === 2 && handleClick(event);
  };
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);

  const handleClickForSaw = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorElForSaw(event.currentTarget);
  };
  const handleCloseForSaw = () => {
    setAnchorElForSaw(null);
  };

  const openForSaw = Boolean(anchorElForSaw);
  const handleCloseAddTask = () => {
    setOpenAddTask(false);
  };
  const handleOpenAddTask = () => {
    setOpenAddTask(true);
  };
  const handleCloseAddNote = () => {
    setOpenAddNote(false);
  };
  const handleOpenAddNote = () => {
    setOpenAddNote(true);
  };
  const handleCloseRemoveModal = () => {
    setOpenRemoveModal(false);
  };
  const handleOpenRemoveModal = () => {
    setOpenRemoveModal(true);
  };
  const dataPopover = [
    {
      title: "Javob berish",
      icon: returnIcon,
      type: "return",
      // onClick: console.log("javob berish"),
    },
    {
      title: "Tahrirlash",
      icon: editIcon,
      type: "edit",
      // onClick: console.log("tahrirlash"),
    },
    {
      title: "Vazifaga belgilash",
      icon: taskIcon,
      type: "task",
      onClick: () => handleOpenAddTask(),
    },
    {
      title: "Majlisga belgilash",
      icon: addAssemblyIcon,
      type: "add-assembly",
      // onClick: console.log("majlisga belgilash"),
    },
    {
      title: "Eslatmaga belgilash",
      icon: notificationIcon,
      type: "notification",
      onClick: () => handleOpenAddNote(),
    },
    {
      title: "Xabarni o’chirish",
      icon: removeIcon,
      type: "remove",
      onClick: () => handleOpenRemoveModal(),
    },
  ];
  const dataPopoverWithSaw = [
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1570295999919-56ceb5ecca61?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8YXZhdGFyfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60",
      name: "Habibjon",
    },
  ];
  return (
    <Wrapper>
      <OpenTaskModal open={openAddTask} handleClose={handleCloseAddTask} />
      <AddNoteModal open={openAddNote} handleClose={handleCloseAddNote} />
      <RemoveModal
        open={openRemoveModal}
        handleClose={handleCloseRemoveModal}
        title="Ushbu habarni o'chirmoxchimisiz"
      />
      {!messageData.byMe && (
        <AvatarPart>
          <img src={messageData.byWhom.avatar} alt="avatar" />
        </AvatarPart>
      )}
      <MessageContent onMouseDown={doubleClick} byMe={messageData.byMe}>
        <FunctionsInPopover
          anchorOrigin={{
            vertical: "center",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
          anchorEl={anchorEl}
          open={open}
          handleClose={handleClose}
        >
          <PopoverFunctions>
            {dataPopover.map((item) => (
              <PopoverItem key={item.type} onClick={item.onClick}>
                <img src={item.icon} alt="icon" />
                <p style={item.type === "remove" ? { color: "#F31B1B" } : {}}>
                  {item.title}
                </p>
              </PopoverItem>
            ))}
            <p className="for-border"></p>
            <PopoverItem onMouseDown={(event: any) => handleClickForSaw(event)}>
              <img src={sawIcon} alt="icon" />
              <p style={{ color: "#06D6A0" }}>50 ta odam ko’rdi</p>
            </PopoverItem>
          </PopoverFunctions>
        </FunctionsInPopover>
        {!messageData.byMe && (
          <h3>
            {messageData.byWhom.name} <span>{messageData.byWhom.position}</span>
          </h3>
        )}
        <BottomPart>
          <h4>{messageData.message}</h4>
          <p>{messageData.time}</p>
        </BottomPart>
      </MessageContent>
      <FunctionsInPopover
        anchorOrigin={{ vertical: "center", horizontal: "right" }}
        transformOrigin={{ vertical: "top", horizontal: "left" }}
        anchorEl={anchorElForSaw}
        open={openForSaw}
        handleClose={handleCloseForSaw}
      >
        <PopoverFunctionsForSaw>
          {dataPopoverWithSaw.map((item, idx) => (
            <PopoverItemForSaw key={idx}>
              <img src={item.avatar} alt="avatar" />
              <p>{item.name}</p>
              <img src={doubleClickIcon} alt="icon" />
            </PopoverItemForSaw>
          ))}
        </PopoverFunctionsForSaw>
      </FunctionsInPopover>
    </Wrapper>
  );
};

export default Message;
