import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  height: 100vh;
  border-radius: 0 7px 7px 0;
  background: #fff;
`;
export const TopPart = styled.div`
  padding: 21.5px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #0000001a;
  h1 {
    font-size: 16px;
    font-weight: 600;
  }
`;
export const ButtonsWithIcon = styled.div`
  display: flex;
  img {
    margin-left: 13px;
    cursor: pointer;
  }
`;
export const Staffs = styled.div`
  padding: 0 24px;
  height: calc(100% - 361px);
  overflow-y: auto;
`;
export const AvatarPart = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 24px;
  img {
    width: 124px;
    object-fit: cover;
    height: 124px;
    border-radius: 50%;
  }
  h2 {
    font-size: 16px;
    margin-top: 16px;
    font-weight: 600;
  }
  p {
    font-size: 14px;
    color: #00000099;
    margin-top: 4px;
    font-weight: 400;
  }
`;
export const AddStaff = styled.div`
  padding: 35px 26px;
  cursor: pointer;
  display: flex;
  border-bottom: 1px solid #0000001a;
  img {
    margin-right: 7px;
  }
  h4 {
    color: #0496ff;
    font-size: 16px;
    font-weight: 500;
  }
`;

export const ChatInfo = styled.div`
  display: flex;
  align-items: center;
  padding: 12px 0;
`;
export const StaffAvatarPart = styled.div`
  img {
    width: 48px;
    height: 48px;
    object-fit: cover;
    margin-right: 8px;
    border-radius: 50%;
  }
`;
export const InfoPart = styled.div`
  width: 100%;
`;
export const TopInfo = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;
export const ChatName = styled.h3`
  font-size: 16px;
  font-weight: 600;
`;
export const ChatNameAndType = styled.div`
  img {
    width: 22px;
    height: 22px;
    margin-top: 3px;
    margin-right: 8px;
  }
  display: flex;
  align-items: center;
`;
export const ChooseImg = styled.div`
  position: relative;
  input {
    display: none;
  }
  width: 124px;
  height: 124px;
  border-radius: 50%;
  img {
    width: 100%;
    object-fit: cover;
    height: 100%;
    border-radius: 50%;
  }
`;
export const ForChooseAvatar = styled.div`
  width: 124px;
  height: 124px;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #00000099;
  border-radius: 50%;
  cursor: pointer;
  img {
    width: 24px;
    height: 24px;
  }
`;
export const EditChatData = styled.div`
  padding: 37px 24px;
`;
export const AboutPrivateChat = styled.div`
  h2 {
    margin-top: 35px;
    font-size: 16px;
    font-weight: 500;
    padding: 0 24px;
  }
`;
export const StaffTasks = styled.div`
  display: flex;
  margin: 0 24px;
  justify-content: space-between;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 45px;
  box-shadow: 0px 5px 11px 0px #0000000f;
`;
export const TaskType = styled.div`
  padding: 10px 9px;
  max-width: 77px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  :nth-last-child(2) {
    border-right: 1px solid #00000010;
    border-left: 1px solid #00000010;
  }
  flex-direction: column;
  h2 {
    font-size: 20px;
    font-weight: 700;
    margin-top: 7px;
  }
  h4 {
    font-size: 12px;
    font-weight: 400;
  }
`;

export const StaffInfo = styled.div``;
export const Info = styled.div`
  padding: 24px;
  border-top: 1px solid #00000010;
  h3 {
    font-size: 16px;
    font-weight: 400;
  }
  h4 {
    font-size: 14px;
    font-weight: 400;
    color: #00000099;
    margin-top: 5px;
  }
`;
export const EditStaffInfo = styled.div`
  .css-113qkos-MuiSwitch-root
    .MuiSwitch-switchBase.Mui-checked
    + .MuiSwitch-track {
    background-color: #0496ff;
  }
  margin-top: 37px;
  padding: 0 24px;
  h3 {
    margin-bottom: 17px;
    margin-top: 49px;
  }
`;
export const Positions = styled.div``;
export const Position = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  h1 {
    font-size: 17px;
    font-weight: 400;
  }
  padding: 6.5px 0;
`;
