import { FC, useState } from "react";
import {
  ChatType,
  ChatTypes,
  Wrapper,
} from "./styles-choose-chat-type-panel";
import { ChooseChatTypePanelProps } from "./types-choose-chat-type-panel";
import forAll from "@assets/images/dashboard/chat/forAll.svg";
import privateChat from "@assets/images/dashboard/chat/privateChat.svg";
import group from "@assets/images/dashboard/chat/group.svg";
import chanel from "@assets/images/dashboard/chat/chanel.svg";
import projects from "@assets/images/dashboard/chat/projects.svg";

import forAllActive from "@assets/images/dashboard/chat/active-imgs/forAll.svg";
import privateChatActive from "@assets/images/dashboard/chat/active-imgs/privateChat.svg";
import groupActive from "@assets/images/dashboard/chat/active-imgs/group.svg";
import chanelActive from "@assets/images/dashboard/chat/active-imgs/chanel.svg";
import projectsActive from "@assets/images/dashboard/chat/active-imgs/projects.svg";
import ChatInfo from "../chose-chat/chat-info/chat-info";
const ChooseChatTypePanel: FC<ChooseChatTypePanelProps> = () => {
  const [activeId, setActiveId] = useState<number>(0);
  const dataForChooseChatType = [
    {
      activeIcon: forAllActive,
      icon: forAll,
      type: "for-all",
      unreadMessages: 4,
    },
    {
      activeIcon: privateChatActive,
      icon: privateChat,
      type: "private-chat",
      unreadMessages: 0,
    },
    { activeIcon: groupActive, icon: group, type: "group", unreadMessages: 12 },
    {
      activeIcon: chanelActive,
      icon: chanel,
      type: "chanel",
      unreadMessages: 1,
    },
    {
      activeIcon: projectsActive,
      icon: projects,
      type: "projects",
      unreadMessages: "99+",
    },
  ];
  
  return (
    <Wrapper>
      
      <ChatTypes>
        {dataForChooseChatType.map((chat, idx) => (
          <ChatType
            className={idx === activeId ? "active" : ""}
            key={idx}
            onClick={() => setActiveId(idx)}
          >
            <img
              src={idx === activeId ? chat.activeIcon : chat.icon}
              alt={chat.type}
            />
            {!!chat.unreadMessages && <p>{chat.unreadMessages}</p>}
          </ChatType>
        ))}
      </ChatTypes>
      
    </Wrapper>
  );
};

export default ChooseChatTypePanel;
