import { FC, useEffect, useState } from 'react';
import Chat from '@components/Layouts/chat/chat';
import {
  useCompanies,
  useCompanyChatGroup,
  useGroupChatList,
  usePersonalChatList,
  useProjectGroupChatList,
} from '@src/hooks';
import { ChatMenu } from '@src/types';

const ChatsPage: FC<{}> = () => {
  const [chatType, setChatType] = useState<ChatMenu>('for-all');
  const [chatData, setChatData] = useState<any>();
 

  return (
    <Chat chatType={chatType} setChatType={setChatType} chatData={chatData} />
  );
};

export default ChatsPage;
