import { FC, useEffect, useState } from "react";
import {
  AvatarPart,
  BottomInfo,
  ChatName,
  ChatNameAndType,
  ChatTime,
  InfoPart,
  LastMessage,
  TopInfo,
  Wrapper,
} from "./styles-chat-info";
import { ChatInfoProps } from "./types-chat-info";
import forAll from "@assets/images/dashboard/chat/forAll.svg";
import group from "@assets/images/dashboard/chat/group.svg";
import chanel from "@assets/images/dashboard/chat/chanel.svg";

const ChatInfo: FC<ChatInfoProps> = ({ data, chatIcon }) => {
  const [currentChatType, setCurrentChatType] = useState<string>("");
  useEffect(() => {
    switch (data.chatType) {
      case "for-all":
        setCurrentChatType(forAll);
        break;
      case "group":
        setCurrentChatType(group);
        break;
      case "chanel":
        setCurrentChatType(chanel);
        break;
      default:
        setCurrentChatType("");
    }
  }, [data]);
  return (
    <Wrapper>
      <AvatarPart style={!data.avatar ? { padding: "8px" } : {}}>
        <img src={data.avatar ? data.avatar : chatIcon} alt="avatar" />
      </AvatarPart>
      <InfoPart>
        <TopInfo>
          <ChatNameAndType>
            {currentChatType && <img src={currentChatType} alt="icon" />}
            <ChatName>{data.chatName}</ChatName>
          </ChatNameAndType>
          <ChatTime>{data.lastMessage.time}</ChatTime>
        </TopInfo>
        <BottomInfo>
          <LastMessage>
            <p>
              {data.lastMessage.whom}: <span>{data.lastMessage.message}</span>
            </p>
          </LastMessage>
        </BottomInfo>
      </InfoPart>
    </Wrapper>
  );
};

export default ChatInfo;
