import styled from "styled-components";

export const ChoseChats = styled.div`
  padding: 12px 24px;
  height: calc(100vh - 229.61px);
  background: #fff;
  border-radius: 0 0 0 7px
  overflow-y: auto;
  ::-webkit-scrollbar {
    width: 0px;
  }
  > h1 {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 12px;
  }
  @media (max-width: 1370px) {
    padding: 12px 16px;
  }
`;
export const ChatsBox = styled.div``;
