import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  border-radius: 7px 0 0 7px;
  background: #fff;
  height: max-content;
`;

export const ChatTypes = styled.div`
  padding: 24px 11px;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  width: 100%;
  overflow-x: auto;
  ::-webkit-scrollbar {
    width: 0;
  }
  justify-content: space-between;
  align-items: center;
  .active {
    background: #0496ff;
    :hover {
      background: #0496ff;
    }
  }
`;
export const ChatType = styled.div`
  cursor: pointer;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 50%;
  :hover {
    background: #e0e0e0;
  }
  p {
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    content: "";
    padding: 2px 6px;
    border-radius: 25px;
    background: #ef476f;
    position: absolute;
    bottom: 0;
    right: 0;
  }
  @media (max-width: 1370px) {
    img {
      width: 25px;
      height: 25px;
    }
    padding: 2px;
    p {
      font-size: 10px;
      padding: 1px 4px;
    }
  }
`;
