import { FC } from "react";
import { TextField } from "@mui/material";
import {
  Input,
  LeftReturnMessage,
  MediaSend,
  ReturnMessage,
  ReturnMessageInfo,
  RightReturnMessage,
  SendBox,
  SendRight,
  Smile,
  Wrapper,
} from "./styles-sending-panel";
import { SendingPanelProps } from "./types-sending-panel";
import sendRight from "@assets/images/dashboard/chat/sendRight.svg";
import emojis from "@assets/images/dashboard/chat/smile.svg";
import mediaSend from "@assets/images/dashboard/chat/mediaSend.svg";
import returnIcon from "@assets/images/dashboard/chat/return-blue.svg";
import closeIcon from "@assets/images/dashboard/chat/close.svg";
const SendingPanel: FC<SendingPanelProps> = () => {
  return (
    <Wrapper>
      <ReturnMessage>
        <LeftReturnMessage>
          <img src={returnIcon} alt="icon" />
          <ReturnMessageInfo>
            <h2>Jahongir</h2>
            <p>Let me know</p>
          </ReturnMessageInfo>
        </LeftReturnMessage>
        <RightReturnMessage>
          <img src={closeIcon} alt="icon" />
        </RightReturnMessage>
      </ReturnMessage>
      <SendBox id="send-input">
        <Smile>
          <img src={emojis} alt="emojis" />
        </Smile>
        <MediaSend>
          <label htmlFor="media-send">
            <img src={mediaSend} alt="mediaSend" />
          </label>
          <input type="file" id="media-send" style={{ display: "none" }} />
        </MediaSend>
        <Input>
          <TextField
            id="standard-textarea"
            placeholder="Xabar kiriting:"
            multiline
            variant="standard"
          />
        </Input>
        <SendRight>
          <img src={sendRight} alt="sendRight" />
        </SendRight>
      </SendBox>
    </Wrapper>
  );
};

export default SendingPanel;
