import { FC, useEffect, useState } from "react";
import {
  AvatarPart,
  BottomInfo,
  ChatInfo,
  ChatName,
  ChatNameAndType,
  DeleteAndCall,
  HeaderChatInfo,
  InfoPart,
  LastMessage,
  TopInfo,
  Wrapper,
} from "./styles-correspondence-panel";
import { CorrespondencePanelProps } from "./types-correspondence-panel";
import forAll from "@assets/images/dashboard/chat/forAll.svg";
import group from "@assets/images/dashboard/chat/group.svg";
import chanel from "@assets/images/dashboard/chat/chanel.svg";
import projects from "@assets/images/dashboard/chat/projects.svg";
import trash from "@assets/images/dashboard/chat/trash.svg";
import call from "@assets/images/dashboard/chat/call.svg";
import Correspondences from "./correspondences/correspondences";
import SendingPanel from "./sending-panel/sending-panel";
import RemoveModal from "../../../common/remove-modal/remove-modal";
const CorrespondencePanel: FC<CorrespondencePanelProps> = ({ chatInfo }) => {
  const [currentChatType, setCurrentChatType] = useState<string>("");
  const [openRemoveModal, setOpenRemoveModal] = useState(false);
  const handleCloseRemoveModal = () => {
    setOpenRemoveModal(false);
  };
  const handleOpenRemoveModal = () => {
    setOpenRemoveModal(true);
  };

  useEffect(() => {
    switch (chatInfo.chatType) {
      case "for-all":
        setCurrentChatType(forAll);
        break;
      case "group":
        setCurrentChatType(group);
        break;
      case "chanel":
        setCurrentChatType(chanel);
        break;
      case "projects":
        setCurrentChatType(projects);
        break;
      default:
        setCurrentChatType("");
    }
  }, [chatInfo]);

  return (
    <Wrapper>
      <HeaderChatInfo>
        <ChatInfo>
          <AvatarPart>
            <img src={chatInfo.avatar} alt="avatar" />
          </AvatarPart>
          <InfoPart>
            <TopInfo>
              <ChatNameAndType>
                {currentChatType && <img src={currentChatType} alt="icon" />}
                <ChatName>{chatInfo.chatName}</ChatName>
              </ChatNameAndType>
            </TopInfo>
            {currentChatType && (
              <BottomInfo>
                <LastMessage>
                  <p>{chatInfo.totalFollowers} ta a’zodan iborat</p>
                </LastMessage>
              </BottomInfo>
            )}
          </InfoPart>
        </ChatInfo>
        <DeleteAndCall>
          {!currentChatType && <img src={call} alt="call" />}
          <img onClick={handleOpenRemoveModal} src={trash} alt="trash" />
          <RemoveModal
            title="Ushbu chatni o'chirmoxchimisiz"
            open={openRemoveModal}
            handleClose={handleCloseRemoveModal}
          />
        </DeleteAndCall>
      </HeaderChatInfo>
      <Correspondences />
      <SendingPanel />
    </Wrapper>
  );
};

export default CorrespondencePanel;
