import styled from "styled-components";

export const Wrapper = styled.div`
  height: 100vh;
  position: relative;
  border-right: 1px solid #0000001a;
  border-left: 1px solid #0000001a;
`;

export const HeaderChatInfo = styled.div`
  background: #fff;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 12px 30px;
  /* padding: 12px 0; */
  justify-content: space-between;
`;

export const ChatInfo = styled.div`
  display: flex;
  align-items: center;
`;
export const AvatarPart = styled.div`
  img {
    width: 40px;
    height: 40px;
    object-fit: cover;
    margin-right: 8px;
    border-radius: 50%;
  }
`;
export const InfoPart = styled.div`
  width: 100%;
`;
export const TopInfo = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;
export const ChatName = styled.h3`
  font-size: 16px;
  font-weight: 600;
`;
export const BottomInfo = styled.div`
  display: flex;
  margin-left: 30px;
  align-items: center;
`;
export const LastMessage = styled.div`
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
`;
export const ChatNameAndType = styled.div`
  img {
    width: 22px;
    height: 22px;
    margin-top: 3px;
    margin-right: 8px;
  }
  display: flex;
  align-items: center;
`;
export const DeleteAndCall = styled.div`
  display: flex;
  img {
    margin-left: 26px;
    width: 24px;
    cursor: pointer;
    height: 24px;
  }
`;
