import { FC, useState } from "react";
import ChatInfo from "./chat-info/chat-info";
import { ChatsBox, ChoseChats } from "./styles-chose-chat";
import { ChoseChatProps } from "./types-chose-chat";

const ChoseChat: FC<ChoseChatProps> = ({ dataForChoseChats }) => {
  const [activeId, setActiveId] = useState<number>(0);
  console.log(dataForChoseChats[activeId]);

  return (
    <ChoseChats>
      <h1>{dataForChoseChats[activeId].chatTitle}</h1>
      <ChatsBox>
        {dataForChoseChats[activeId].chats.map((chat: any, idx: number) => (
          <ChatInfo
            data={chat}
            key={idx}
            chatIcon={dataForChoseChats[activeId].chatIcon}
          />
        ))}
      </ChatsBox>
    </ChoseChats>
  );
};

export default ChoseChat;
