import {
  DateInput,
  DropDownMenu,
  Input,
  MuiSlider,
  Selector,
  TimeInput,
} from '@components/common';
import { FC, useState } from 'react';
import { useForm } from 'react-hook-form';
import { AddNoteForm, AddNoteProps } from './types-add-note';
import pin from '@assets/images/dashboard/pin.svg';
import {
  Card,
  DayBox,
  DayCard,
  Form,
  NoteTime,
  RepeatBox,
  RepeatCard,
  SlideCard,
  Wrapper,
} from './styles-add-note';
import { ModalButton } from '@components/Layouts/staff/optional-styles';

export const PinMenu = [
  {
    icon: null,
    title: 'O’zgartira olamaydi',
  },
  {
    icon: null,
    title: 'O’zgartira oladi',
  },
];

const AddNote: FC<AddNoteProps> = () => {
  const [selectedValue, setSelectedValue] = useState<string>('');
  const { handleSubmit, control } = useForm<AddNoteForm>();
  const onChange = (type: string, value: any) => {};
  const onSelect = (value: string) => {};
  return (
    <Wrapper>
      <h1>Eslatma qo'shish</h1>
      <Form>
        <Input
          title="Eslatma nomi"
          placeholder="Eslatma"
          name="note"
          control={control}
          onChange={onChange}
        />
        <Input
          title="Izoh nomi"
          placeholder="Izoh"
          name="description"
          control={control}
          onChange={onChange}
        />
      </Form>
      <Card>
        <NoteTime>
          <p>Eslatma vaqti</p>
          <div>
            <TimeInput />
            <DateInput />
            <DropDownMenu
              children={<img src={pin} alt="pin" />}
              bordered={false}
              menuData={PinMenu}
              forNotification={false}
              background="#F4F5F7"
              onSelect={onSelect}
            />
          </div>
        </NoteTime>
        <RepeatCard>
          <RepeatBox>
            <h2>Takroriyligi</h2>
            <Selector
              title=""
              placeholder="Takroriylik"
              options={[
                'Bir marta',
                'Har kun',
                'Har hafta',
                'Har oy',
                'Har yil',
              ]}
              defaultValue='Har hafta'
              selectedValue={setSelectedValue}
            />
          </RepeatBox>
          <DayCard>
            <DayBox>Du</DayBox>
            <DayBox active>Se</DayBox>
            <DayBox>Ch</DayBox>
            <DayBox active>Pa</DayBox>
            <DayBox>Ju</DayBox>
            <DayBox active>Sh</DayBox>
            <DayBox>Ya</DayBox>
          </DayCard>
        </RepeatCard>
      </Card>
      <SlideCard>
          <h3>Eslatma</h3>
        <MuiSlider />
      </SlideCard>
      <ModalButton
        width="30%"
        padding="18px 0"
        background="#0496FF"
        color="#fff"
      >
        Saqlash
      </ModalButton>
    </Wrapper>
  );
};

export default AddNote;
